import React from 'react';


const Overview = props => {
    return (  
<div>
<p>The creation of new enterprises, the different stages throughout the life of start-ups as well as their exit is key to analyse the clean energy transition. Since new firms bring disruptive technologies into the market, they will also be one of the vectors through which technologies enabling the Energy Transition can be developed and commercialised.</p>
<p>The indicators we developed aim at covering the full lifetime of energy start-ups from creation to financing, also exploring the possible exits offered to them.</p>
<h2>Crunchbase provides a database of 1.6M start-ups with many associated tables that include useful elements:</h2>
<ul>
              <li>Founding dates of start-ups</li>
              <li>Funding amounts and types throughout their lifetime</li>
              <li>Description of the start-ups, including text and sector classification</li>
              <li>Details of the IPO (when relevant)</li>
              <li>Details of the acquisition (when relevant)</li>
              <li>People working in the company</li>
        </ul>
</div>
    
    )
}

export default Overview;